<template>
  <div>
    <div class="form-container">
      <ProgressCircle :current-step="3" :total-steps="7"></ProgressCircle>
      <h2 class="form-heading">{{ $t('form.amenitiesTitle') }}</h2>
      <div class="form-input">
        <label>
          {{ $t('form.PropertyAmenities') }}
        </label>
        <ChipSelector
          value="amenity_id"
          label="amenity_name"
          @update-selection="updatePropertyAmenities"
          :options="propertyOptions"
          :defaultValue="property.form3.propertyAmenities"></ChipSelector>
      </div>
      <div class="form-input">
        <label>
          {{ $t('form.kitchenAmenities') }}
        </label>
        <ChipSelector
          value="amenity_id"
          label="amenity_name"
          @update-selection="updateKitchenAmenities"
          :options="kitchenOptions"
          :defaultValue="property.form3.kitchenAmenities"></ChipSelector>
      </div>
      <div class="form-input">
        <label>
          {{ $t('form.bathroomAmenities') }}
        </label>
        <ChipSelector
          value="amenity_id"
          label="amenity_name"
          @update-selection="updateBathroomAmenities"
          :options="bathroomOptions"
          :defaultValue="property.form3.bathroomAmenities"></ChipSelector>
      </div>
    </div>

    <div class="page-actions">
      <b-button @click="goBack">{{ $t('common.back') }}</b-button>
      <b-button variant="primary" @click="goNext">
        {{ $t('common.next') }}
      </b-button>
    </div>
  </div>
</template>

<script>
import ChipSelector from '@/components/shared/form/chipSelector';
import ProgressCircle from '@/components/postingRequestForm/ProgressCircle';

export default {
  name: 'AddPropertyPage3',
  props: {
    propertyData: {
      type: Object,
      required: true,
    },
  },
  components: {
    ChipSelector,
    ProgressCircle,
  },
  transition: {
    name: 'slide-fade',
    mode: 'out-in',
  },
  data() {
    return {
      currentProperty: null,
      property: {
        form1: null,
        form2: null,
        form3: {
          propertyAmenities: null,
          kitchenAmenities: null,
          bathroomAmenities: null,
        },
        form4: null,
        form5: null,
        form6: null,
      },
    };
  },
  head() {
    return {
      title: `${this.dir === "ltr" ? "Add Property | Step 3" : "إضافة عقار | الخطوة 3"} ${this.appEnv !== 'production' ? '| Staging' : ''}`,
      meta: [
        // hid is used as unique identifier. Do not use `vmid` for it as it will not work
        {
          hid: 'description',
          name: 'description',
          content: this.dir === "ltr" ? 'Add Property | Property Amenities' : 'إضافة عقار | وسائل الراحة الخاصة بالعقار'
        }
      ]
    }
  },
  computed: {
    propertyOptions() {
      return this.propertyData.amenities?.Property;
    },
    kitchenOptions() {
      return this.propertyData.amenities?.Kitchen;
    },
    bathroomOptions() {
      // It Will Be Dynamically depend on Category Choice
      return this.propertyData.amenities?.['Residential Bathroom'];
    },
    dir() {
      return this.$store.getters['CoreModule/isRTL'] ? 'rtl' : 'ltr';
    },
    appEnv(){
      return this.$config.APP_ENV
    },
  },
  methods: {
    updatePropertyAmenities(item) {
      this.property.form3.propertyAmenities = item;
    },
    updateKitchenAmenities(item) {
      this.property.form3.kitchenAmenities = item;
    },
    updateBathroomAmenities(item) {
      this.property.form3.bathroomAmenities = item;
    },
    goNext() {
      const { propertyAmenities, kitchenAmenities, bathroomAmenities } =
        this.property.form3;
      if (
        !propertyAmenities?.length ||
        !kitchenAmenities?.length ||
        !bathroomAmenities?.length
      ) {
        window.scrollTo({
          top: 120,
          left: 0,
          behavior: 'smooth',
        });
        let body = this.$t('form.checkListFirst');
        this.$bvToast.toast(body, {
          title: this.$t('form.checkListFirst'),
          variant: 'primary',
          toaster: 'b-toaster-top-center',
          solid: true,
          autoHideDelay: 2000,
        });
        return;
      }
      localStorage.setItem(
        'property',
        JSON.stringify({
          ...this.currentProperty,
          form3: this.property.form3,
        })
      );
      this.$router.push(this.localePath('/properties/add/4'));
    },
    goBack() {
      this.$router.push(this.localePath('/properties/add/2'));
    },
  },
  mounted() {
    window.scrollTo({
      top: 0,
      left: 0,
      behavior: 'smooth',
    });
    this.currentProperty = localStorage.getItem('property') && JSON.parse(localStorage.getItem('property'));

    if (!this.currentProperty)
      this.$router.push(this.localePath('/properties/add/1'));
    if (this.currentProperty.form3) this.property = this.currentProperty;
  },
};
</script>

<style></style>
